import React from "react";
import Card from "react-bootstrap/Card";
import CardDeck from "react-bootstrap/CardDeck";
import "./static/css/PortfolioSection.css";

const PortfolioSection = (props) => {
  return (
    <div>
      <Card id="Portfolio-Section">
        <Card.Body>
          <Card.Title>Portfolio</Card.Title>
        </Card.Body>
      </Card>
      <CardDeck
        style={{
          paddingLeft: "20px",
          paddingRight: "20px",
          paddingBottom: "20px",
        }}
      >
        <Card>
          <Card.Img variant="top" src="./static/img/twitterclone.PNG" />
          <Card.Body>
            <Card.Title>Twitter Clone</Card.Title>
            <Card.Text>
              <p>This is a clone of Twitter's page using Bootstrap.</p>
              <p>
                The top of the page is built using the navbar class. The rest of
                the page is divided into three columns, the left, center and
                right. The left column consists of a list of header classes. The
                center column consists of a list of article classes that
                represents the tweets. The right column consists of lists of
                images, spans and headers.
              </p>
            </Card.Text>
            <Card.Link
              href="https://lohengliang.github.io/twitter-clone/"
              target="_blank"
            >
              Project Website
            </Card.Link>
          </Card.Body>
        </Card>
        <Card>
          <Card.Img variant="top" src="./static/img/thestraitstimesclone.png" />
          <Card.Body>
            <Card.Title>The Straits Times Clone</Card.Title>
            <Card.Text>
              <p>This is a clone of The Straits Times' page using React.</p>{" "}
              <p>
                The webpage consists of six React components, representing
                things like header and footer. Quite a number of css adjustments
                are needed to make the page close to the real version.
              </p>
            </Card.Text>
            <Card.Link
              href="https://lohengliang.github.io/the-straits-times-clone/"
              target="_blank"
            >
              Project Website
            </Card.Link>
          </Card.Body>
        </Card>
        <Card>
          <Card.Img variant="top" src="./static/img/todolistclone.png" />
          <Card.Body>
            <Card.Title>To Do List Clone</Card.Title>
            <Card.Text>
              <p>
                This is a full stack implementation of a to do list. User can
                add item to the list as well as mark it as completed. He can
                then filtered the list to show uncompleted items.
              </p>
              <p>
                The frontend is built using React and Redux, and the backend is
                built using Express. The application is linked to a PostgreSQL
                database.
              </p>
            </Card.Text>
            <Card.Link
              href="https://lohengliang.github.io/to-do-list/"
              target="_blank"
            >
              Project Website
            </Card.Link>
          </Card.Body>
        </Card>
      </CardDeck>
      <CardDeck
        style={{
          paddingLeft: "20px",
          paddingRight: "20px",
          paddingBottom: "20px",
        }}
      >
        <Card>
          <Card.Img variant="top" src="./static/img/lennonwall.PNG" />
          <Card.Body>
            <Card.Title>Lennon Wall</Card.Title>
            <Card.Text>
              <p>This is a full stack implementation of Lennon Wall.</p>
              <p>
                User can create a wall by adding the wall name to the end of the
                url. User can then login to create a post that will display
                his/her name or create a post anonymously.
              </p>
              <p>
                The frontend is built using React and Redux, and the backend is
                built using Express. The application is linked to a PostgreSQL
                database.
              </p>
            </Card.Text>
            <Card.Link
              href="https://desolate-eyrie-51440.herokuapp.com/"
              target="_blank"
            >
              Project Website
            </Card.Link>
          </Card.Body>
        </Card>
        <Card>
          <Card.Img variant="top" src="./static/img/instagram.png" />
          <Card.Body>
            <Card.Title>Instagram</Card.Title>
            <Card.Text>
              <p>
                This is a full stack implementation of Instagram like
                application.
              </p>
              <p>User can upload, view and delete images.</p>
              <p>
                The frontend is built using React and Redux, and the backend is
                built using Firebase Storage and Firestore.
              </p>
            </Card.Text>
            <Card.Link
              href="https://instagram-55ddc.firebaseapp.com/"
              target="_blank"
            >
              Project Website
            </Card.Link>
          </Card.Body>
        </Card>
        <Card>
          <Card.Img variant="top" src="./static/img/pitchbooker.png" />
          <Card.Body>
            <Card.Title>Pitch Booker</Card.Title>
            <Card.Text>
              <p>This is a full stack implementation of soccer pitch booker.</p>
              <p>Pitch providers can create, update and delete pitches.</p>
              <p>
                Pitch bookers can search for available pitches within a time
                slot and create a booking.
              </p>
              <p>
                The frontend is built using React, and the backend is built
                using Express. The application is linked to a PostgreSQL
                database.
              </p>
            </Card.Text>
            <Card.Link
              href="https://pitch-booker.herokuapp.com/"
              target="_blank"
            >
              Project Website
            </Card.Link>
          </Card.Body>
        </Card>
      </CardDeck>
      <CardDeck
        style={{
          paddingLeft: "20px",
          paddingRight: "20px",
          paddingBottom: "20px",
        }}
      >
        <Card>
          <Card.Img variant="top" src="./static/img/whatsup.png" />
          <Card.Body>
            <Card.Title>WhatsUp</Card.Title>
            <Card.Text>
              <p>This is a mobile implementation of a chatting application.</p>
              <p>
                User can create an user account and login to view the chat room.
                User can then send message or delete his own message.
              </p>
              <p>
                The frontend is built using Flutter, and it is linked to
                Firestore.
              </p>
            </Card.Text>
            <Card.Link
              href="https://whatsup-1153b.firebaseapp.com/#/"
              target="_blank"
            >
              Project Website
            </Card.Link>
          </Card.Body>
        </Card>
        <Card style={{ border: "0px" }}></Card>
        <Card style={{ border: "0px" }}></Card>
      </CardDeck>
    </div>
  );
};

export default PortfolioSection;
